.services {
  padding-top: 79px;
  padding-bottom: 30px;

  @include media-tablet {
    padding-top: 8px;
    padding-bottom: 20px;
  }
}

.services__container {
  @include container();
  padding-left: 15px;
  padding-right: 10px;

  @include media-tablet {
    padding-left: 25px;
    padding-right: 10px;
  }
}

.services__title {
  margin-top: 20px;
  margin-bottom: 17px;
  padding-right: 5px;

  display: flex;
  align-items: center;
  position: relative;
  font-size: 30px;
  @include font-raleway-black();
  color: $color-black;
  text-transform: uppercase;
  line-height: 1.667;

  &::after {
    content: "";
    width: 100%;
    margin-left: 185px;
    height: 3px;
    background-color: $color-black;
  }

  @include media-tablet {
    margin-top: 5px;
    margin-bottom: 24px;
    font-size: 26px;
    line-height: 1.2;
    &::after {
      display: none;
    }
  }

  @include media-mobile {
    font-size: 20px;
  }
}

.services__list {
  @include menu();
  align-items: flex-start;

  @include media-tablet {
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 20px;
  }

  @include media-mobile {
    height: auto;
  }
}

.services__item {
  box-sizing: border-box;
  box-shadow: 0 5px 9px 1px rgba(0, 0, 0, 0.05);
  padding-left: 41px;
  padding-right: 41px;
  min-height: 500px;
  margin-bottom: 41px;

  flex-basis: 31%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 39px;

  &:nth-child(3n) {
    margin-right: 0;
  }

  @media (max-width: 1200px) {
    flex-basis: 30%;
    padding-left: 20px;
    padding-right: 20px;
  }

  @include media-tablet {
    min-height: auto;
    box-shadow: none;
    width: 48%;
    justify-content: flex-start;
    flex-basis: auto;
    margin-bottom: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 10px;
  }
}

.services__header {
  font-size: 16px;
  @include font-raleway-black();
  color: $color-black;
  text-transform: uppercase;
  line-height: 1.2;
  margin-top: 37px;
  margin-bottom: 23px;

  @include media-tablet {
    box-sizing: border-box;
    width: 100%;
    @include font-roboto-regular();
    position: relative;
    font-size: 14px;
    //line-height: 1.857;
    margin: 0;

    display: flex;
    flex-direction: column;
    justify-content: center;

    //padding-top: 11px;
    //padding-bottom: 11px;
    min-height: 50px;

    padding-left: 19px;
    padding-right: 19px;

    text-align: left;
    background-color: #f4f4f4;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      width: 8px;
      height: 5px;
      top: 41%;
      transform: translateY(50%);
      right: 19px;
      background-image: url("../img/service-toggle.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      opacity: 0.3;
    }

    &--opened {
      background-color: #d7d7d7;
    }

    &--opened::after {
      opacity: 1;
    }

    &--opened + .services__block {
      display: flex;
    }
  }
}

.no-js .services__block {
  display: flex;
}

.services__block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include media-tablet {
    padding: 0;
    display: none;
    background-color: #f4f4f4;
  }
}

.services__link {
  font-size: 13px;
  @include font-roboto-regular();
  color: rgba(51, 51, 51, 0.769);
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 15px;

  &:hover {
    text-decoration: underline;
  }

  @include media-tablet {
    box-sizing: border-box;
    display: flex;
    font-size: 14px;
    flex-direction: column;
    justify-content: center;
    min-height: 47px;
    width: 100%;
    margin-bottom: 0;
    padding-left: 18px;
    padding-right: 18px;
    border: 1px solid #f4f4f4;

    span {
      display: none;
    }

    &:hover {
      text-decoration: none;
      border-width: 1px;
      border-color: rgb(0, 255, 215);
      border-style: solid;
      box-shadow: 0 2px 2px 0.5px rgba(0, 255, 216, 0.65);

    }

  }
}
